UpdateProduct.css

.container-fluid {
    padding: 20px;
  }
  
  .text-center {
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  .form-control,
  .form-select {
    width: 100%;
  }
  
  .img-fluid {
    max-width: 100%;
    height: auto;
  }
  
  @media (max-width: 768px) {
    .form-group {
      margin-bottom: 1rem;
    }
  
    .btn {
      font-size: 0.875rem;
    }
  }
  