/* AdminDashboardStyles.css */

.admin-dashboard {
    padding: 20px;
    background: #f4f4f4;
    min-height: 100vh;
  }
  
  .profile-card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .profile-card h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .profile-info {
    font-size: 18px;
    color: #555;
  }
  
  .profile-info p {
    margin-bottom: 15px;
  }
  
  .profile-info strong {
    color: #007bff;
  }
  
  @media (max-width: 768px) {
    .profile-card {
      width: 100%;
      padding: 15px;
    }
  
    .profile-card h2 {
      font-size: 20px;
    }
  
    .profile-info {
      font-size: 16px;
    }
  }
  
  @media (max-width: 480px) {
    .profile-card {
      padding: 10px;
    }
  
    .profile-card h2 {
      font-size: 18px;
    }
  
    .profile-info {
      font-size: 14px;
    }
  }
  