/* CreateProductStyles.css */

/* General Container */
.create-product-container {
    padding: 20px;
    background: #f9f9f9;
    min-height: 100vh;
  }
  
  .page-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  .create-product-form {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .upload-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .img {
    max-width: 100%;
    height: auto;
  }
  
  @media (max-width: 768px) {
    .create-product-form {
      padding: 0 15px;
    }
  
    .page-title {
      font-size: 1.5rem;
    }
  }
  
  @media (max-width: 576px) {
    .create-product-form {
      padding: 0;
    }
  
    .upload-btn {
      font-size: 0.875rem;
    }
  }
  