/* DashboardStyles.css */

.dashboard-container {
    margin: 20px;
    padding: 20px;
    background: #f9f9f9;
  }
  
  .profile-card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .profile-title {
    font-size: 24px;
    color: #333;
    margin-bottom: 15px;
    text-align: center;
  }
  
  .profile-info {
    font-size: 18px;
    color: #555;
    margin-bottom: 10px;
  }
  
  .profile-info strong {
    color: #000;
  }
  
  @media (max-width: 768px) {
    .profile-card {
      padding: 15px;
      max-width: 100%;
    }
  
    .profile-title {
      font-size: 20px;
    }
  
    .profile-info {
      font-size: 16px;
    }
  }
  
  @media (max-width: 480px) {
    .profile-card {
      padding: 10px;
    }
  
    .profile-title {
      font-size: 18px;
    }
  
    .profile-info {
      font-size: 14px;
    }
  }
  