.banner-img {
  width: 100%;
  height: auto;
  margin-top: 60px;
  object-fit: cover;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
}

.home-page h1 {
  color: #333;
  font-family: "Playfair Display", serif;
  text-align: center;
  margin-bottom: 20px;
}

.filters {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.filters h4 {
  color: #333;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
  margin-bottom: 15px;
}

.filter-options {
  margin-bottom: 20px;
}

.reset-filters {
  background-color: #dc3545;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  font-weight: bold;
}

.reset-filters:hover {
  background-color: #c82333;
}

.products-section {
  padding: 15px;
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 300px;
}

.card-img-top {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.card-img-top:hover {
  transform: scale(1.05);
}

.card-body {
  padding: 15px;
}

.card-title {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 10px;
}

.card-price {
  font-size: 1.1rem;
  color: #28a745;
  font-weight: bold;
  margin-bottom: 10px;
}

.card-text {
  color: #555;
  margin-bottom: 15px;
}

.card-actions {
  display: flex;
  justify-content: space-between;
}

.btn-info, .btn-dark {
  border-radius: 5px;
  font-weight: bold;
}

.btn-info {
  background-color: #007bff;
  color: #fff;
}

.btn-info:hover {
  background-color: #0056b3;
}

.btn-dark {
  background-color: #343a40;
  color: #fff;
}

.btn-dark:hover {
  background-color: #23272b;
}

.loadmore {
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: bold;
  display: block;
  margin: 20px auto;
  cursor: pointer;
}

.loadmore:hover {
  background-color: #218838;
}

@media (max-width: 768px) {
  .home-page {
    flex-direction: column;
  }

  .filters {
    margin-bottom: 20px;
  }

  .products-grid {
    gap: 10px;
  }

  .card {
    max-width: 100%;
  }
}
