/* ProductsStyles.css */

.products-container {
    padding: 20px;
    background-color: #f8f9fa;
  }
  
  .text-center {
    text-align: center;
  }
  
  .products-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }
  
  .product-card {
    text-decoration: none;
    color: inherit;
    width: 100%;
    max-width: 300px;
  }
  
  .card {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .card-img-top {
    width: 100%;
    height: auto;
  }
  
  .card-body {
    padding: 15px;
  }
  
  .card-title {
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
  }
  
  .card-text {
    font-size: 1rem;
    color: #555;
  }
  
  @media (max-width: 768px) {
    .products-grid {
      justify-content: center;
    }
  }
  
  @media (max-width: 576px) {
    .card-body {
      padding: 10px;
    }
  
    .card-title {
      font-size: 1rem;
    }
  
    .card-text {
      font-size: 0.875rem;
    }
  }
  