.category {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 80px;
}

.category .card {
  background-color: rgba(128, 128, 128, 0.1);
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  max-width: 18rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.category .card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.category .card-img-top {
  height: 300px;
  object-fit: cover;
}

.category .card-name-price {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f9f9f9;
}

.category .card-price {
  color: green;
  font-weight: bold;
}

.category .card-text {
  color: #5a5a5a;
  padding: 0 10px 10px;
}

.category button {
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.category button:hover {
  background-color: #0056b3;
}
