.product-details {
  margin-top: 20px;
}

.product-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.product-info {
  padding: 20px;
}

.product-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 15px;
}

.product-description {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 15px;
}

.product-price {
  font-size: 1.5rem;
  color: #e74c3c;
  margin-bottom: 15px;
}

.product-category {
  font-size: 1rem;
  color: #888;
  margin-bottom: 20px;
}

.add-to-cart {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 4px;
}

.add-to-cart:hover {
  background-color: #218838;
}

.related-products {
  margin-top: 40px;
}

.related-products .card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

.related-products .card-img-top {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.related-products .card-body {
  padding: 15px;
}

.card-title {
  font-size: 1.25rem;
  color: #333;
}

.card-price {
  font-size: 1.2rem;
  color: #e74c3c;
}

.card-text {
  font-size: 0.9rem;
  color: #666;
}

.btn-info {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 8px 15px;
  font-size: 0.9rem;
  border-radius: 4px;
}

.btn-info:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  .product-title {
    font-size: 1.5rem;
  }

  .product-description {
    font-size: 1rem;
  }

  .product-price {
    font-size: 1.2rem;
  }

  .related-products .card-img-top {
    height: 150px;
  }
}

@media (max-width: 576px) {
  .product-title {
    font-size: 1.25rem;
  }

  .product-description {
    font-size: 0.9rem;
  }

  .product-price {
    font-size: 1rem;
  }
}
