.policy-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .policy-banner {
    width: 100%;
    max-height: 400px;
    overflow: hidden;
  }
  
  .banner-img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .policy-content {
    max-width: 800px;
    width: 100%;
    margin-top: 20px;
  }
  
  .policy-title {
    text-align: center;
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 20px;
    font-weight: bold;
  }
  
  .policy-text {
    font-size: 1rem;
    line-height: 1.6;
    color: #555;
  }
  
  .policy-text ul {
    margin: 20px 0;
    padding-left: 20px;
  }
  
  .policy-text ul li {
    margin-bottom: 10px;
  }
  
  @media (max-width: 768px) {
    .policy-title {
      font-size: 2rem;
    }
  
    .policy-text {
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 576px) {
    .policy-title {
      font-size: 1.5rem;
    }
  
    .policy-text {
      font-size: 0.8rem;
    }
  }
  