/* CreateCategoryStyles.css */

/* General Container */
.dashboard {
    padding: 20px;
    background: #f9f9f9;
    min-height: 100vh;
  }
  
  /* Page Title */
  .page-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Form Container */
  .form-container {
    max-width: 600px;
    margin-bottom: 30px;
  }
  
  /* Table Container */
  .table-container {
    overflow-x: auto;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .table th, .table td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .table th {
    background-color: #007bff;
    color: #fff;
  }
  
  .table td {
    color: #333;
  }
  
  .btn {
    font-size: 1rem;
    padding: 8px 12px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .btn-primary {
    background-color: #007bff;
    border: none;
    color: #fff;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
  }
  
  .btn-danger {
    background-color: #dc3545;
    border: none;
    color: #fff;
  }
  
  .btn-danger:hover {
    background-color: #c82333;
  }
  
  /* Modal Styling */
  .category-modal .ant-modal-body {
    padding: 20px;
  }
  
  @media (max-width: 768px) {
    .form-container {
      max-width: 100%;
    }
  
    .table-container {
      overflow-x: scroll;
    }
  }
  
  @media (max-width: 576px) {
    .page-title {
      font-size: 1.5rem;
    }
  
    .btn {
      font-size: 0.875rem;
      padding: 6px 10px;
    }
  }
  