/* ProfileStyles.css */

.profile-container {
    margin: 20px;
    padding: 20px;
    background: #f9f9f9;
  }
  
  .form-container {
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .form-title {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-control {
    width: 100%;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #ddd;
    font-size: 16px;
    box-sizing: border-box;
    transition: border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  
  .form-control:focus {
    border-color: #007bff;
    box-shadow: 0 0 0 3px rgba(38, 143, 255, 0.2);
    outline: none;
  }
  
  .btn-primary {
    background-color: #007bff;
    border: none;
    color: #fff;
    padding: 12px;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    width: 100%;
    transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
    transform: translateY(-2px);
  }
  
  .btn-primary:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(38, 143, 255, 0.3);
  }
  
  @media (max-width: 768px) {
    .form-container {
      padding: 15px;
      max-width: 100%;
    }
  
    .form-title {
      font-size: 20px;
    }
  
    .form-control {
      font-size: 14px;
    }
  }
  
  @media (max-width: 480px) {
    .form-container {
      padding: 10px;
    }
  
    .form-title {
      font-size: 18px;
    }
  
    .form-control {
      font-size: 12px;
    }
  }
  