.contact-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 40px;
    background-color: #f8f9fa;
  }
  
  .contact-image {
    flex: 1;
  }
  
  .contact-img {
    width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .contact-info {
    flex: 1;
    max-width: 100%;
  }
  
  .contact-header {
    background-color: #343a40;
    color: #ffffff;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .contact-description {
    font-size: 1.125rem;
    color: #495057;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .contact-details {
    font-size: 1rem;
    color: #343a40;
  }
  
  .contact-details p {
    display: flex;
    align-items: center;
    margin: 10px 0;
  }
  
  .contact-icon {
    font-size: 1.5rem;
    margin-right: 10px;
    color: #007bff;
  }
  
  .contact-details a {
    color: #007bff;
    text-decoration: none;
  }
  
  .contact-details a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 768px) {
    .contact-container {
      flex-direction: column;
    }
  }
  